    proveedores: IProveedor[];
    public dataSource: MatTableDataSource<IProveedor> = new MatTableDataSource();
    headers = ['CUIT', 'Razon Social', 'Dirección', 'Localidad', 'Teléfono', 'Email', 'Contacto', 'acciones']
    column_params = ['cuit', 'RazonSocial', 'Direccion', 'localidad_descripcion', 'telefono', 'Mail', 'contacto', 'acciones']
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['RazonSocial', 'cuit'];
    filter: IFilter = {
        activo: -1
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.administracion.view_proveedores + '?sort=Descripcion';
    search: FormControl = new FormControl('');
    sortValue: string;
