import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IProveedor } from '../models/proveedor';
import { ILocalidad } from '@shared/models/localidad';

@Injectable()
export class ProveedoresService {
    constructor(private http: HttpClient) { }
/**
 * -Funcion en el servicio- 
 * - Esta funcion retorna los proveedores.
 * - Configura los filtros para el paginado. 
 * @param filter 
 * @returns proveedores
 */
    getProveedores(filter: any): Observable<IProveedor[]> {
        //Se aplican filtro, o no, para el paginado.
        let f = '?';
        f += filter?.sort == 'desc' ? `sort=-RazonSocial&` : ''
        f += filter?.pagination ? 'pagination=1&' : '';
        f += filter?.perpage ? 'per-page=' + filter.perpage + '&' : '';
        f += filter?.currentPage ? 'page=' + filter.currentPage + '&' : '';

        var subject = new Subject<any>();
        this.http.get<IProveedor[]>(`${environment.administracion.view_proveedores}${f}`, { observe: 'response' })
            .subscribe((resp: any) => {
                subject.next({
                    'list': resp.body,
                    'totalPages': resp.headers.get('x-pagination-page-count'),
                    'totalCount': resp.headers.get('x-pagination-total-count')
                })
            });
        return subject.asObservable();
    }

    getProveedor(idp: number): Observable<IProveedor> {
        return this.http.get<IProveedor>(`${environment.administracion.view_proveedores}/${idp}`);
    }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los localidades .
     * @returns localidades 
     */
    getLocalidades(filter?: string): Observable<ILocalidad[]> {
        return this.http.get<ILocalidad[]>(`${environment.localidades}${filter} `);
    }
    /**
     * Funcion en el Servicio 
     * @param proveedor 
     * @returns Un proveedor nuevo
     */
    saveProveedor(proveedor: IProveedor): Observable<IProveedor> {
        return this.http.post<IProveedor>(`${environment.administracion.proveedores}`, proveedor);
    }
    /**
     * Funcion en el servicio
     * @param proveedor 
     * @returns proveedor actualizado
     */
    updateProveedor(proveedor: IProveedor): Observable<IProveedor> {
        return this.http.put<IProveedor>(`${environment.administracion.proveedores}` + '/' + proveedor.Codigo, proveedor);
    }
    /**
     * Funcion en el servicio
     * @param codigoProveedor 
     * @returns Elimina el proveedor
     */
    deleteProveedor(codigoProveedor: number): Observable<IProveedor> {
        return this.http.delete<IProveedor>(environment.administracion.proveedores + '/' + codigoProveedor);
    }
}